import Vue from 'vue'
import Router from 'vue-router'
const login = () => import('@/components/login')
const home = () => import('@/components/home')
const userList = () => import('@/components/user/userList')
const agentList = () => import('@/components/user/agentList')
const agentInfo = () => import('@/components/user/agentInfo')
const userInfo = () => import('@/components/user/userInfo')
const userPower = () => import('@/components/user/userPower')
const detailPage = () => import('@/components/user/detailPage')
const appList = () => import('@/components/app/appList')
const appDetail = () => import('@/components/app/appDetail')
const terminal = () => import('@/components/terminal/terminal')
const terDetail = () => import('@/components/terminal/terDetail')
const set = () => import('@/components/setting/set')
const open = () => import('@/components/setting/open')
const web = () => import('@/components/setting/web')
const pay = () => import('@/components/setting/pay')
const message = () => import('@/components/setting/message')
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'login',
      component: login
    },{
      path: '/home',
      name: 'home',
      component: home,
      children:[
        {
          path:'/detailPage/:id',
          name:'detailPage',
          component:detailPage
        },
        {
          path:'/home',
          redirect:'/userList',
          component:userList
        },
        {
          path:'/userList',
          name:'userList',
          component:userList
        },{
          path:'/agentList',
          name:'agentList',
          component:agentList
        },{
          path:'/agentInfo/:id',
          name:'agentInfo',
          component:agentInfo
        },
        {
          path:'/userPower',
          name:'userPower',
          component:userPower
        },
        {
          path:'/appList',
          name:'appList',
          component:appList
        },
         {
          path:'/appDetail/:id',
          name:'appDetail',
          component:appDetail
        },
        {
          path:'/terminal',
          name:'terminal',
          component:terminal
        },
        {
          path:'/terDetail/:id',
          name:'terDetail',
          component:terDetail
        },
        {
          path:'/set',
          name:'set',
          component:set
        },{
          path:'/web',
          name:'web',
          component:web
        },{
          path:'/pay',
          name:'pay',
          component:pay
        },{
          path:'/message',
          name:'message',
          component:message
        },{
          path:'/userInfo/:id',
          name:'userInfo',
          component:userInfo
        }
      ]
    }
  ]
})



